'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';

type ConnectionStatus = {
  success: boolean;
  message: string;
  details?: any;
};

export default function GoogleSheetsTestPage() {
  const [status, setStatus] = useState<ConnectionStatus | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function testConnection() {
    try {
      setLoading(true);
      setError(null);
      
      const timestamp = new Date().getTime();
      const response = await fetch(`/api/ordain/test?t=${timestamp}`, {
        method: 'GET',
        cache: 'no-store',
        headers: {
          'Pragma': 'no-cache',
          'Cache-Control': 'no-cache'
        }
      });
      
      const result = await response.json();
      setStatus(result);
    } catch (err) {
      console.error('Test failed:', err);
      setError('เกิดข้อผิดพลาดในการทดสอบการเชื่อมต่อ');
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    // ทดสอบการเชื่อมต่อเมื่อโหลดหน้า
    testConnection();
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-b from-blue-50 to-purple-50 py-10">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white rounded-lg shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-gray-200">
            <h1 className="text-2xl font-bold text-blue-800">
              ทดสอบการเชื่อมต่อกับ Google Sheets API
            </h1>
          </div>
          
          <div className="px-6 py-4">
            <div className="mb-4 flex space-x-4">
              <button
                onClick={testConnection}
                disabled={loading}
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:bg-blue-300"
              >
                {loading ? 'กำลังทดสอบ...' : 'ทดสอบการเชื่อมต่อ'}
              </button>
              
              <Link href="/api/ordain/test" target="_blank" className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700">
                ดูข้อมูล API โดยตรง
              </Link>
            </div>
            
            {error && (
              <div className="mb-4 p-4 bg-red-50 text-red-600 rounded-md">
                <p className="font-semibold">เกิดข้อผิดพลาด:</p>
                <p>{error}</p>
              </div>
            )}
            
            {status && (
              <div className={`mb-6 p-4 ${status.success ? 'bg-green-50 text-green-700' : 'bg-red-50 text-red-600'} rounded-md`}>
                <p className="font-semibold mb-2">สถานะการเชื่อมต่อ: {status.success ? '✅ สำเร็จ' : '❌ ล้มเหลว'}</p>
                <p className="mb-2">ข้อความ: {status.message}</p>
              </div>
            )}
            
            {status && status.success && status.details?.sampleData && (
              <div className="mb-6">
                <h2 className="text-xl font-semibold mb-3 text-blue-700">ตัวอย่างข้อมูลที่ดึงได้:</h2>
                <div className="overflow-x-auto">
                  <table className="min-w-full border-collapse">
                    <thead>
                      <tr className="bg-blue-100">
                        {Array.from({ length: status.details.sampleData[0]?.length || 0 }).map((_, i) => (
                          <th key={i} className="px-4 py-2 border border-blue-200">คอลัมน์ {i+1}</th>
                        ))}
                      </tr>
                    </thead>
                    <tbody>
                      {status.details.sampleData.map((row: any[], rowIndex: number) => (
                        <tr key={rowIndex} className={rowIndex % 2 === 0 ? 'bg-white' : 'bg-blue-50'}>
                          {row.map((cell, cellIndex) => (
                            <td key={cellIndex} className="px-4 py-2 border border-blue-200">
                              {cell !== null && cell !== undefined ? String(cell) : 'ไม่มีข้อมูล'}
                            </td>
                          ))}
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}
            
            {status && !status.success && status.details && (
              <div className="mb-6">
                <h2 className="text-xl font-semibold mb-3 text-red-600">รายละเอียดข้อผิดพลาด:</h2>
                <pre className="p-4 bg-gray-100 rounded-md overflow-x-auto text-sm">
                  {JSON.stringify(status.details, null, 2)}
                </pre>
              </div>
            )}
            
            <div className="mt-6 p-4 bg-yellow-50 rounded-md">
              <h2 className="text-lg font-semibold text-yellow-700 mb-2">คำแนะนำในการแก้ไขปัญหา:</h2>
              <ul className="list-disc pl-5 text-yellow-800">
                <li>ตรวจสอบว่าตั้งค่าตัวแปรสภาพแวดล้อมครบถ้วน (GOOGLE_CLIENT_EMAIL, GOOGLE_PRIVATE_KEY, GOOGLE_SPREADSHEET_ID)</li>
                <li>ตรวจสอบว่า Service Account มีสิทธิ์เข้าถึง Google Sheet ที่ระบุ</li>
                <li>ตรวจสอบว่า GOOGLE_PRIVATE_KEY มีการแทนที่ \n อย่างถูกต้อง</li>
                <li>ตรวจสอบว่า GOOGLE_SPREADSHEET_ID ถูกต้อง</li>
              </ul>
            </div>

            <div className="mt-6 p-4 bg-blue-50 rounded-md">
              <h2 className="text-lg font-semibold text-blue-700 mb-2">การตรวจสอบตัวแปรสภาพแวดล้อม:</h2>
              <p className="mb-4">สร้างไฟล์ <code>.env.local</code> ที่รากของโปรเจค และกำหนดค่าดังนี้:</p>
              <pre className="p-4 bg-gray-100 rounded-md overflow-x-auto text-sm">
{`GOOGLE_CLIENT_EMAIL="ordain@ordainapi.iam.gserviceaccount.com"
GOOGLE_PRIVATE_KEY="-----BEGIN PRIVATE KEY-----\\nMIIE...PRIVATE KEY HERE...==\\n-----END PRIVATE KEY-----\\n"
GOOGLE_SPREADSHEET_ID="1irWdri3mJdCnpmxtLjvqsq00JN769tLVGr7wbuDlqL4"`}
              </pre>
            </div>
          </div>
          
          <div className="px-6 py-4 bg-gray-50 border-t border-gray-200">
            <Link href="/" className="text-blue-600 hover:text-blue-800">
              กลับสู่หน้าหลัก
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}